body {
  margin: 0;
  padding: 0;
  background-color: #f2f2f2; }

* {
  font-family: "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

.upper {
  margin: 2em;
  text-align: center; }
  .upper h1 {
    color: #9da2a7; }
  .upper .searchbar input {
    width: 50vw;
    border: none;
    padding: 7px;
    background-color: #fbfbfb;
    box-shadow: 1px 2px 2px 0 rgba(0, 0, 0, 0.14); }
  .upper .searchbar input::-webkit-input-placeholder {
    color: #9da2a7; }
  .upper .searchbar input::-ms-input-placeholder {
    color: #9da2a7; }
  .upper .searchbar input::placeholder {
    color: #9da2a7; }
  .upper .searchbar input:focus {
    background-color: white;
    box-shadow: none;
    color: inherit; }
  .upper .searchbar button {
    padding: 7px 16px;
    border: none;
    box-shadow: 1px 2px 2px 0 rgba(0, 0, 0, 0.14);
    color: white;
    background-color: #14d188;
    font-weight: bold; }

.lower {
  padding: 2em; }
  @media only screen and (max-width: 1300px) {
    .lower .gallery {
      grid-template-columns: 1fr 1fr !important; } }
  @media only screen and (max-width: 850px) {
    .lower .gallery {
      grid-template-columns: 1fr !important; } }
  .lower .gallery {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-gap: 2em; }
    .lower .gallery .card {
      background-color: white;
      border-radius: 10px;
      padding: 1em;
      height: 80%;
      margin-bottom: 2em; }
      .lower .gallery .card .grid {
        display: grid;
        grid-template-columns: 2fr 3fr;
        grid-gap: 1em;
        height: 100%; }
        .lower .gallery .card .grid img {
          margin: auto;
          margin-top: -2em; }
        .lower .gallery .card .grid .details {
          position: relative; }
          .lower .gallery .card .grid .details p,
          .lower .gallery .card .grid .details h4 {
            margin: 0;
            color: dimgray; }
          .lower .gallery .card .grid .details p {
            font-size: 0.8em; }
          .lower .gallery .card .grid .details h4 {
            margin-bottom: 5px; }
          .lower .gallery .card .grid .details .btn {
            position: absolute;
            bottom: 0;
            width: 100%; }
            .lower .gallery .card .grid .details .btn a {
              margin: auto;
              display: block;
              width: 80%;
              background-color: #14d188;
              border: none;
              border-radius: 5px;
              padding: 6px 0;
              color: white;
              font-weight: bold;
              text-decoration: none;
              text-align: center;
              font-size: 0.8em; }
            .lower .gallery .card .grid .details .btn a:hover {
              background-color: #10ad70; }
  .lower .notgallery {
    color: #9da2a7;
    text-align: center;
    font-weight: bold; }
    .lower .notgallery span {
      padding-right: 0.5em;
      font-size: 1.2em; }

