.toast,
.toast2 {
  visibility: hidden;
  height: 50px;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin: auto;
  background-color: #d14444;
  color: #fff;
  text-align: center;
  border-radius: 10px;
  position: fixed;
  z-index: 1;
  left: 0;
  right: 0;
  top: 30px;
  font-size: 17px;
  white-space: nowrap; }

.toast .desc,
.toast2 .desc {
  color: #fff;
  padding: 16px;
  overflow: hidden;
  white-space: nowrap; }

.toast.show,
.toast2.show {
  visibility: visible;
  -webkit-animation: fadein 1s, fadeout 1s 3s;
          animation: fadein 1s, fadeout 1s 3s; }

@-webkit-keyframes fadein {
  from {
    top: 0;
    opacity: 0; }
  to {
    top: 30px;
    opacity: 1; } }

@keyframes fadein {
  from {
    top: 0;
    opacity: 0; }
  to {
    top: 30px;
    opacity: 1; } }

@-webkit-keyframes fadeout {
  from {
    top: 30px;
    opacity: 1; }
  to {
    top: 0;
    opacity: 0; } }

@keyframes fadeout {
  from {
    top: 30px;
    opacity: 1; }
  to {
    top: 0;
    opacity: 0; } }

